import React, { useEffect, useState } from "react";
import { Icon, List } from "antd-mobile";
import { observer, inject } from "mobx-react";
import EasyNarBar from "@/components/EasyNarBar/EasyNarBar";
import { IPerformDetailViewItem } from "./store";
import PerformDetailViewItem from "./item";
import { useLocation, useHistory } from "react-router-dom";
import ListView from "@/components/ListView/listview";
const PerformDetailView = observer((props: any) => {
  const location = useLocation();
  const history = useHistory();
  const [height, setHeight] = useState(0);
  const { dataSource, initStore, clearData, isLoading, hashMore, performDetailViewFetch, onRefresh } = props.store;
  useEffect(() => {
    const state = location.state;
    if (state) {
      clearData();
      initStore(state);
      const NarBar = document.querySelector(".am-navbar");
      let Height = Math.max(document.documentElement.clientHeight, document.body.clientHeight);
      Height = Height - (NarBar ? NarBar.offsetHeight : 0);
      setHeight(Height);
    }
  }, []);
  const onEndReached = () => {
    if (!isLoading && hashMore) {
      performDetailViewFetch();
    }
  };
  const separator = (sectionID: string, rowID: string) => (
    <div
      key={`${sectionID}-${rowID}`}
      style={{
        backgroundColor: "#F5F5F9",
        width: "100%",
        height: "0",
        paddingLeft: "34px",
        borderBottom: "1px solid #ECECED"
      }}
    />
  );
  const onLeftClick = () => {
    history.goBack();
  };
  const renderRow = (rowData: any) => {
    return <PerformDetailViewItem model={rowData} />;
  };
  return (
    <div>
      <EasyNarBar mainContent="履职详情" leftContent={<Icon type="left" />} onLeftClick={onLeftClick}></EasyNarBar>
      <ListView
        dataSource={dataSource}
        renderRow={renderRow}
        separator={separator}
        pageSize={20}
        onEndReached={onEndReached}
        height={height}
        isLoading={isLoading}
        hasMore={hashMore}
      ></ListView>
    </div>
  );
});

export default inject((stores: any) => ({
  store: stores.performDetailViewStore
}))(PerformDetailView);
