import React, { useEffect } from "react";
import { observer } from "mobx-react";
import { IPerformDetailViewItem } from "./store";
require("./style.scss");
type IPropsItem = {
  model: IPerformDetailViewItem;
};

const PerformDetailViewItem = observer((props: any) => {
  const { devname, areaandposition, logtime } = props.model;
  return (
    <>
      <div className="perform_detail_view_item">
        <div className="perform_detail_view_item_deviceName">{devname}</div>
        <div className="perform_detail_view_item_deviceLocation">{areaandposition ? areaandposition : "-"}</div>
        <div className="perform_detail_view_item_operateTime">{logtime}</div>
      </div>
    </>
  );
});

export default PerformDetailViewItem;
